.newsletter{
    padding: 120px 0;
    background: url("/img/newsletter/newsletter_main-img.webp") center center/cover no-repeat;
    .title-block{
        h2{
            color: #fff;
            letter-spacing: 1.44px;
        }
        h3{
            color: #fff;
        }
    }
    form{
        display: flex;
        justify-content: center;
        padding-top: 90px;
        @include row-flex();
        input{
            @include xs(margin, 0 15px 20px 15px);
            @include lg(margin,0);
            width: 500px;
            height: 60px;
            border: 1px solid #444957;
            padding: 0 25px;
            background-color:rgba(#212121, 0.8);
        }
        button{
            width: 187px;
            height: 60px;
            margin-top: 1px;
            @include xs(border-radius,0);
            @include lg(border-top-right-radius,5px);
            @include lg(border-bottom-right-radius,5px);
        }
    }
}