@import "modules/header/header";
@import "modules/first/first";
@import "modules/roadmap/roadmap";
@import "modules/about/about";
@import "modules/token/token";
@import "modules/technologies/tech";
@import "modules/ico_descr/ico_descr";
@import "modules/ico_details/ico_details";
@import "modules/faq/faq";
@import "modules/newsletter/newsletter";
@import "modules/footer/footer";
@font-face {
    font-family: 'Myriad Pro';
    src: url('../fonts/MyriadPro-Regular.eot');
    src: url('../fonts/') format('embedded-opentype'),
    url('../fonts/MyriadPro-Regular.woff') format('woff'),
    url('../fonts/MyriadPro-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
    }
*{
    font-family: 'Montserrat','Lato','Open Sans', sans-serif;
   
}
button{
    width: 200px;
    height: 50px;
    background-image: linear-gradient(to right, #0087cd 0%, #5400ff 100%);
    border: 0;
    color: #ffffff;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    cursor: pointer;
}
.title-block{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    @include col();
    @include size-lg(8);
    margin: 0 auto;
    
    &__title{
        color: #0e1421;
        font-size: 36px;
        font-weight: 700;
        line-height: 44.05px;
        /* Text style for "Company Ro" */
        letter-spacing: -1.8px;
        margin-bottom: 19px;
    }
    &__descr{
        color: #919191;
        font-family: "Myriad Pro";
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        margin-bottom: 19px;
    }
    hr{
        width:100px; 
        border-style: solid;
        border-color: #69d2e7;;
       
    }
}
ul{
    list-style-type: none;
}