.roadmap{
    .container{
        @include md(padding-bottom,232px)
    }
    .title-block{
        margin-top: 112px;
    }
    &__timeline{
        @include xs(margin-top, 20px);
        @include md(margin-top, 333px);
        width: 100%;
        list-style-type: none;
        display: flex;
        
        @include xs(flex-wrap, wrap);
        @include md(flex-wrap, initial);
        justify-content: space-around;
        li{
            @include xs(width, 100%);
            @include md(width, 50%);
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            position: relative;
            @include xs(margin-bottom, 50px);
            @include md(margin-bottom, 0);
            .point{
                @include xs(display, none);
                @include md(display, block);
                width: 12px;
                height: 12px;
                box-shadow: 0 5px 0 rgba(255, 255, 255, 0.12);
                border: 6px solid #f7f7f7;
                background-color: #38bae8;
                border-radius: 100%;
                margin-bottom: 50px;
                
            }
            .date{
                &__title{
                    font-size: 25px;
                    font-weight: 400;
                    margin-bottom: 15px;
                }
                &__descr{
                    color: #919191;
                    font-size: 14px;
                    font-weight: 400;
                    letter-spacing: 0.14px;
                }
            }
            &:first-child::before{
                content: "";
                @include xs(display, none);
                @include md(display, block);
                display: block;
                width: 57%;
                right: 50%;
                height: 5px;
                border: 1px solid #f0f0f2;
                background-color: #38bae8;
                opacity: 0.3;
                position: absolute;
                top: 9px;
                z-index: -1;
                border-radius: 10px;  
            }
            &::before{
                content: "";
                @include xs(display, none);
                @include md(display, block);
                display: block;
                width: 98%;
                right: 50%;
                height: 5px;
                border: 1px solid #f0f0f2;
                background-color: #38bae8;
                opacity: 0.3;
                position: absolute;
                top: 9px;
                z-index: -1;
            }
            &:last-child::before{
                content: "";
                @include xs(display, none);
                @include md(display, block);
                display: block;
                width: 98%;
                left: -50%;
                height: 5px;
                border: 1px solid #f0f0f2;
                background-color: #38bae8;
                opacity: 0.3;
                position: absolute;
                top: 9px;
                z-index: -1;
            }
            &:last-child::after{
                content: "";
                @include xs(display, none);
                @include md(display, block);
                display: block;
                width: 57%;
                left: 50%;
                height: 5px;
                border: 1px solid #f0f0f2;
                background-color: #38bae8;
                opacity: 0.3;
                position: absolute;
                top: 9px;
                z-index: -1;
            }
            &:hover{
                .point{
                    background-color: #000;
                }
               
            }
            &.post_active{
                &::before{
                    background-color: #000;
                    opacity: 1;
                }
                .point{
                    background-color: #000;
                }
            }
            &.post_top{
                position: relative;
                .date{
                    @include xs(position, static);
                    @include xs(top, 0);
                    @include md(position, absolute);
                    @include md(top,-100px);
                    
                }
            }
            &.post_last{
                .date{
                    @include md(top, -145%);
                    display:flex;
                    flex-direction: column-reverse;
                    &__title{
                        padding: 30px 30px;
                        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
                        background-color: #f7f7f7;
                    }
                    &__descr{
                        color: #fff;
                        padding: 22px 47px;
                        background-color: #101523;
                        background-image: linear-gradient(to right, #0087cd 0%, #5400ff 100%);
                    }
                }
            }
        }
    }
    

}