.first{
    background: url("/img/first/main.png") center center/cover no-repeat;
   .container{
       height: 950px ;
       display: flex;
       flex-direction: column;
       justify-content: space-between;
   }
    &__wrapper{
        @include xs(margin-top,300px);
        @include sm(margin-top,300px);
        @include md(margin-top,300px);
        @include lg(margin,414px 0 0 0);
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        h1{
            @include lg(font-size,70px);
            @include sm(font-size,40px);
            font-weight: 900;
            letter-spacing: 4px;
            margin-bottom: 27px;
        }
        h2{
            font-size: 20px;
            color:#ffffff;
            font-weight: 400;
            letter-spacing: 8px;
        }
        button{
            margin-top: 50px;
        }
        
    }
    &__coop-list{
        display: flex;
        align-items: center;
        list-style-type: none;
        @include lg(justify-content,space-between);
        margin-bottom: 49px;
        flex-wrap: wrap;
        @include xs(justify-content, center);
       
        
        li{
            @include col();
            @include xs(margin-top,20px);
        }

    }
}