.token{
    
    background-color: #1a1d31;
    padding-top: 120px; 
    padding-bottom: 50px;
    position: relative;
    .title-block{
        h2{
            color: #ffffff;
        }
        margin-bottom: 45px;
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
        @include row-flex();
    }
    .info{
        text-align: center;
        display: flex;
        flex-direction: column;
        list-style-type: none;
        justify-content: space-between;
        height: 337px;
        @include col();
        @include size-sm(4);
        @include size-lg(3);
        @include size-xl(2);
        @include xs(margin, 0 auto);
        

       
        li{
            width: 188px;
        }
        &__title{
            color: #ffffff;
            font-family: Lato;
            font-size: 35px;
            font-weight: 300;
            line-height: 35.01px;
            letter-spacing: 0.7px;
            margin-bottom: 10px;
        }
        &__descr{
            opacity: 0.5;
            color: #ffffff;
            font-family: Lato;
            font-size: 14px;
            font-weight: 400;
            line-height: 25.01px;
            text-transform: uppercase;
            /* Text style for "UNTIL NEXT" */
            letter-spacing: 0.28px;
        }
        
    }
    &__map{
        @include col();
        @include size-sm(8);
        @include size-xs(12);
        @include sm(order,10);
        @include xl(order,0);
        @include sm(margin,0 auto);
        @include xl(margin, 45px 0 0 0);
        @include xs(margin, 10px auto)
        
    }
    &__btn{
        
        display: block;
        margin: 64px auto 0 auto;
    }
}