.tech{
    background-color: #f5f5f5;
    padding-top: 118px;
    &__list{
        margin-top: 150px;
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li{
            display: flex;
            @include col();
            @include size-lg(6);
            margin-bottom: 100px;
            @include xs(flex-direction,column);
            @include sm(flex-direction,row);
            @include xs(text-align,center);
            @include sm(text-align,left);
            h3{
                color: #0e1421;
                font-size: 12px;
                font-weight: 600;
                text-transform: uppercase;
            }
            img{
                @include col();
                @include size-xl(2);
                @include sm(margin, 0 20px auto 0);
                @include xs(margin, 0 auto 20px auto); 
            }
            hr{
                width: 40px;
                height: 1px;
                background-color: #0e1421;
                @include sm(margin,  34px auto 29px 0);
                @include xs(margin,  34px auto 29px auto);
            }
            div{
                @include col();
                @include size-xl(9);
            }
            &:nth-child(2n){
                img{
                    @include xl(margin-left, auto);
                }
            }
        }
    }
}