.ico-descr{
    background: url("/img/ico/2-layers.webp") center center/cover no-repeat;
    padding: 120px 0;
    
    .title-block{
        &__title{
            color: #fff;
        }
        &__descr{
            color: #fff;
        }
    }
    &__list{
        list-style-type: none;
        @include row-flex();
        justify-content: center;
        padding-top: 70px;
        li{
            display: flex;
            flex-direction: column;
            @include u-col(100px);
            @include size-xl(3);
            @include xs(align-items,center);
            @include xs(text-align,center);
            @include xs(margin-bottom,30px);
            @include lg(margin-bottom,0);
            
            h4{
                color: #ffffff;
                font-family: "Open Sans";
                font-size: 16px;
                font-weight: 600;
                line-height: 24px;
                text-transform: uppercase;
                margin: 25px 0 17px 0;
            }
            h5{
                color: #ffffff;
                font-family: Montserrat;
                font-size: 14px;
                font-weight: 300;
                line-height: 21px;
                width: 191px;
            }
            img{
                @include col();
                @include size-xl(5);
            }
            &:nth-child(2n+1){
                @include md(align-items, flex-end);
                @include md(text-align, right);
                
            }
            &:nth-child(2n){
                @include md(align-items, flex-start);
                @include xs(text-align,left);
            }
        }
    }
}