.about{
    background-color: #f5f5f5;
    height: auto;
    .container{
        padding-top: 121px;
    }
    .title-block{
        margin-bottom: 43px;
    }
    &__wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__img{
        background-color: #f5f5f5;
        @include col();
        @include size-xl(5);
        @include size-md(7);
        @include xs(width, 100%);
        margin: 0 auto;
        @include xs(margin-bottom,15px);
        @include lg(margin-bottom,-10px);
        @include xl(margin-bottom,-0);
    }
    &__text-block{
        @include col();
        
        @include size-lg(5);
        @include xs(text-align,center);
        @include lg(text-align,left);
        @include xs(padding-bottom,15px);
        @include lg(padding-bottom,0);
        h2{
        color: #0e1421;
        font-size: 22px;
        font-weight: 700;
        @include md(line-height, 38.01px); 
        letter-spacing: 0.44px;
        @include xs(margin-bottom, 10px);
        @include lg(margin-bottom, 46px);

        }
        h3{
            color: #7b7b7b;
            font-size: 20px;
            font-weight: 300;
            line-height: 29px;
            @include sm(margin-bottom, 10px);
            @include lg(margin-bottom, 31px);
        }
        p{
            color: #adadad;
            font-size: 15px;
            font-weight: 300;
            line-height: 26px;
           
            @include lg(margin-bottom, 20px)
        }
    }
}