.header{
    min-height: 94px;
    background-color: rgba($color: #000000, $alpha: 0.5);
    position: absolute;
    width: 100%;
    
    nav{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        @include lg(padding-top,52px);
        @include sm(padding-top,20px);
        position: relative;
        .hamburger{
            @include sm(display, none);
            @include xs(display, flex);
            text-align: center;
            width: 50px;
            height: 40px;
            margin: 10px auto;
            background-color: rgba(#000, 0.5);
            position: absolute;
            right: -15px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            z-index: 50;
            span{
                display: block;
                height: 3px;
                width:80%;
                background-color: #fff;
                transition: 0.5s all;
            }
            &_active{
                position: fixed;
                right: 0;
                span{
                    &:nth-child(1){
                        transform: translateY(1px) rotate(-45deg);
                        margin-top: 19px;
                    }
                    &:nth-child(2){
                        display: none;
                    }
                    &:nth-child(3){
                        transform: translateY(1px) rotate(45deg);
                        margin-bottom: 20px;
                    }
                }
            }

        }
    }
    &__logo{
        
        @include col();
        @include size-lg(3);
        @include xs(width,100%);
        @include xs(text-align, center);
        @include lg(text-align, left);
        @include lg(margin,0);
        @include xs(margin, 25px auto 0 auto);
        @include sm(margin, 0 auto 10px auto);
        
    }
    &__menu{
        list-style-type: none;
        
        @include size-lg(6);
        @include size-sm(12);
        justify-content: space-between;
        
        @include sm(height,auto);
        @include sm(position, static);
        @include sm(display,flex);
        @include sm(flex-wrap,wrap);
        @include xs(position, fixed);
        @include xs(width, 50%);
        @include xs(text-align,center);
        @include xs(top, 0);
        @include xs(height,100vh);
        @include xs(background-color, rgba(#000, 0.8));
        @include sm(background-color, rgba(#000, 0));
        @include xs(padding,50px 0);
        @include sm(padding,0);
        @include sm(position,static);
        @include xs(left,-100%);
        z-index: 50;
        transition: 0.5s all;
        &_active{
            @include xs(left,-15px);
        }
        li{
            @include xs(margin-bottom,50px);
            @include md(margin-bottom,0px);
            a{
                text-decoration: none;
                color: #fff;
                font-family: Montserrat, sans-serif;
                font-size: 13px;
                font-weight: 400;
                text-transform: uppercase;
                position: relative;                
                
                &::before{
                    display: block;
                    position: absolute;
                    content: "";
                    height: 5px;
                    width: 0;
                    right: 50%;
                    transform: translateX(50%);
                    bottom: -16px;
                    background-color:#382dee;
                    transition: 0.3s all;
                }
                &:hover::before{
                    width: 175%;
                }
            }
            
        }
    }
    
}
