.faq{
    .title-block{
        margin-bottom: 125px;
    }
    padding: 120px 0 0 0;
    background-color: #f6f6f6;
    &__list{
        @include row-flex();
        li{
            @include col();
            @include size-xl(4);
            @include size-md(6);
            display: flex;
            @include xs(flex-direction, column);
            @include sm(flex-direction, row);
            @include xl(margin-bottom, 115px);
            @include xs(margin-bottom, 50px);
            @include xs(align-items, center);
            @include xs(text-align, center);
            @include sm(align-items, flex-start);
            @include sm(text-align, left);
            h3{
                margin-top: 8px;
                font-size: 16px;
                font-weight: bold;
                line-height: 22px;
                text-transform: uppercase;
                letter-spacing: 0.32px;
            }
            p{
                font-size: 15px;
                font-weight: 300;
                line-height: 25.01px;
                @include sm(margin-left, 6px);
                @include xs(margin-left, 0);
            }
            &:hover{
                h3{
                    color:#66cbe0;;
                }
                .faq__number{
                    background-image: linear-gradient(to right, #0087cd 0%, #5400ff 100%);
                    &::before{
                        top: -12px;
                        left: -12px;
                    }
                }
            }
        }
    }
    &__number{
        @include col();
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50px;
        min-width: 50px;
        background-color: #eaeaea;
        position: relative;
        @include xs(max-width,50px);
         

        &::before{
            content: "";
            display: block;
            width: 30px;
            height: 30px;
            position:  absolute;
            border-top:  2px solid #66cbe0;
            border-left:  2px solid #66cbe0;
            top:-2px;
            left:-2px;
            transition: 0.1s all; 
        }

    }
}