.ico-details{
    padding: 117px 0;
    &__wrapper{
        padding-top: 80px;
        @include row-flex();
        justify-content: center;
        
    }
    &__main-img{
        @include xs(width,100%);
        @include sm(width,auto);
        @include col();
        @include size-xs(12);
        align-self: center;
    }
    &__list{
        @include row-flex();
        @include size-xl(4);
        @include size-xs(12);
        @include xl(margin-left, 130px);
        @include xs(justify-content, space-around);
        @include xs(margin-top, 50px);
        @include xl(margin-top, 0);
        li{
            @include size-xl(12);
            @include size-sm(6);
            @include xs(text-align,center);
            @include xl(text-align,left);
            img{
                @include col();
            }
            h3{
                margin: 15px 0 15px 0;
                font-size: 21px;
                font-weight: bold;
                line-height: 38.01px;
            }
            p{
                font-size: 15px;
                font-weight: 300;
                line-height: 25.01px;
            }
        }
        
    }
}