.footer{
    padding: 50px 0;
    text-align: center;
    &__menu{
        margin: 66px auto 40px auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        max-width: 548px;
        font-size: 13px;
        font-weight: 400;
        text-transform: uppercase;
        letter-spacing: 0.88px;
        
        li{
            @include xs(margin, 0 10px 30px 10px);
            @include sm(margin-bottom,0);
            a{
                text-decoration: none;
                color: #475374;
                &:hover{
                    color: #38bae8;;
                }
            }
        }
    }
    p{
        color: #000000;
        font-size: 12px;
        font-weight: 300;
        letter-spacing: 0.81px;
    }
}